.section-5 {
  background-color: $section-color;

  .wrapper {
    @media (min-width: 1440px) {
      padding: 61px 96px 118px 104px;
    }
  }

  .section-title {
    margin-bottom: 44px;
    text-align: center;
  }

  .section-tagline {
    margin-bottom: 42px;
    text-align: left;
    font-weight: 500;

    @media (min-width: 576px) {
      padding-left: 0;
      padding-right: 0;
    }

    @media (min-width: 1200px) {
      text-align: center;
      padding: 0 170px;
    }

    & br {
      display: none;

      @media (min-width: 1200px) {
        display: inline;
      }
    }
  }
}

.getting-in-touch-col__first {
  @media (min-width: 576px) {
    padding-left: 0;
  }
}

.getting-in-touch-col__last {
  @media (min-width: 576px) {
    padding-right: 0;
  }
}

.work-together {
  padding-left: 5%;
  padding-right: 5%;

  @media (min-width: 465px) {
    padding-left: 10%;
    padding-right: 10%;
  }

  @media (min-width: 576px) {
    padding-left: 0;
    padding-right: 0;
  }
}

.work-together-col {
  padding: 0;
}

.interest-information {
  @media (min-width: 576px) {
    padding-left: 15px;
  }

  @media (min-width: 768px) {
    padding-left: 21px;
  }

  @media (min-width: 992px) {
    padding-left: 42px;
  }

  @media (min-width: 1200px) {
    padding-left: 60px;
  }
}

.personal-information {
  margin-bottom: 18px;

  @media (min-width: 576px) {
    padding-right: 15px;
    margin-bottom: 0;
  }

  @media (min-width: 768px) {
    padding-right: 21px;
  }

  @media (min-width: 992px) {
    padding-right: 42px;
  }

  @media (min-width: 1200px) {
    padding-right: 60px;
  }
}

.form-label {
  display: block;
  margin-bottom: 7px;
  text-align: center;
  font-size: 22px;
  font-weight: 500;

  @media (min-width: 576px) {
    text-align: left;
  }
}

.form-input {
  display: block;
  font-weight: 300;
  font-family: "Source Sans Pro";
  width: 100%;
  height: 50px;
  border-radius: 4px;
  border: 1px solid #cccccc;
  background-color: #ffffff;
  padding: 10px 20px;
}

input {
  height: 44px;
  box-shadow: none;
  -webkit-box-shadow: none;
  display: block;
  width: 100%;
  padding-left: 10px;
  font-size: 16px;
  line-height: 1.5;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
  border-radius: 3px;
  caret-color: $secondary-color;

  -webkit-transition: border-color ease-in-out .15s,-webkit-box-shadow ease-in-out .15s;
  -o-transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
  transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;

  &:focus-within .form-label {
    color: $secondary-color;
  }

  &:focus {
    border-color: $secondary-color;
    outline: none;
  }
}

.inerested-in-input {
  margin-bottom: 18px;
}

.work-together-submit {
  margin-top: 30px;

  @media (min-width: 465px) {
    margin: 0 auto;
    margin-top: 30px;
  }

  @media (min-width: 992px) {
    margin: 0;
    margin-top: 48px;
  }

  @media (min-width: 1200px) {
    margin: 0;
    width: 124px;
    margin-top: 48px;
    color: #ffffff;
    font-size: 22px;
    font-weight: 400;
    line-height: 25px;
  }
}

.getting-in-touch__button {
  margin-top: 48px;
  width: 100%;
  display: flex;
  justify-content: center;

  @media (min-width: 768px) {
    display: block;
  }
}

//DROPDOWN//

.getting-in-touch__dropdown {

  .dropdown {
    width: 100%;
    height: 50px;
    display: inline-block;
    margin-bottom: 18px;
    background-color: #ffffff;
    border: 1px solid #cccccc;
    border-radius: 4px;
    transition: all .5s ease;
    position: relative;
    font-size: 14px;
    color: #474747;
    text-align: left;
  }

  .dropdown .select {
    cursor: pointer;
    display: block;
    padding: 10px 20px;
    min-height: 50px;
    font-size: 16px;
    white-space: nowrap;
    overflow: hidden;
  }

  .select-span {
    max-width: 88%;
    display: block;
    overflow: hidden;
  }

  .dropdown .select > .dropdown-icon {
    position: absolute;
    top: 18px;
    right: 20px;
    font-size: 13px;
    color: #888;
    cursor: pointer;
    transition: all .3s ease-in-out;
    line-height: 20px;
    width: 12px;
    height: 12px;
    border: 5px solid $secondary-color;
    border-top: none;
    border-right: none;
    transform-origin: center;
    transform: rotate(-45deg);
  }

  .dropdown:hover {
    box-shadow: 0 0 4px $secondary-color;
  }

  .dropdown:focus {
    border-color: $secondary-color;
    outline: none;
  }

  .dropdown.active:hover,
  .dropdown.active {
    box-shadow: 0 0 4px rgb(204, 204, 204);
    border-radius: 5px 5px 0 0;
    background-color: #ffffff;
  }

  .dropdown.active .select > .dropdown-icon {
    transform-origin: center;
    transform: rotate(-225deg);
  }

  .dropdown .dropdown-menu {
    width: 88.4%;
    display: none;
    position: absolute;
    background-color: #fff;
    top: 30px;
    left: 0;
    margin-top: 1px;
    box-shadow: 0 0 0 1px #e0e7ee;
    border-radius: 5px;
    overflow: hidden;
    overflow-y: auto;
    z-index: 9;
  }

  .dropdown .dropdown-menu li {
    padding: 10px;
    transition: all .2s ease-in-out;
    cursor: pointer;
    font-size: 16px;
    padding-left: 20px;

    @media (min-width: 576px) {
      font-size: 16px;
    }

    @media (min-width: 992px) {
      font-size: 16px;
    }
  }

  .dropdown .dropdown-menu {
    padding: 0;
    list-style: none;
  }

  .dropdown .dropdown-menu li:hover {
    background-color: $secondary-color;
    color: #ffffff;
    font-size: 16px;
  }

  .dropdown .dropdown-menu li:active {
    background-color: $secondary-color;
  }
}
