.footer-section {
  .wrapper {
    @media (min-width: 1440px) {
      padding-top: 63px;
      padding-bottom: 58px;
    }
  }
}

.our-contacts {
  @media (min-width: 768px) {
    display: flex;
    flex-direction: row;
  }

  .terms {
    margin-bottom: 0;
  }
}

.contacts-col {
  min-width: 100%;
  margin-bottom: 20px;

  &:first-child{
    order: 1;

    @media (min-width: 768px) {
      order: 0;
    }
  }

  &::nth-child(2),
  &::nth-child(3) {

    @media (min-width: 768px) {
      order: 0;
    }
  }

  &:last-child{

    @media (min-width: 768px) {
      order: 0;
    }
  }

  @media (min-width: 768px) {
    min-width: 0;
    margin-bottom: 0;
  }
}

.terms-links {
  display: block;
  color: $footer-text;
  font-size: 18px;
  line-height: 35px;
  letter-spacing: 0.9px;

  &:nth-child(2) {
    margin-bottom: 25px;
  }
}

.blazar-copyright {
  color: #1998a2;
  font-size: 18px;
  font-weight: 500;
}

.blazar-link {
  color: #1998a2;
  position: relative;
  margin-top: 24px;

  &::before {
    position: absolute;
    content: "";
    top: 10px;
    left: -22px;
    width: 16px;
    height: 16px;
    border: 1px solid #1998a2;
    border-radius: 50%;
  }

  &::after {
    position: absolute;
    content: "C";
    top: 0;
    left: -18px;
    font-size: 12px;
    color: #1998a2;
  }
}

.contacts-title {
  display: block;
  color: #1CB8C4;
  font-size: 22px;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 8px;
}

a:hover {
  color: #1CB8C4;
  text-decoration: none;
}

.contacts-info {
  display: block;
  font-size: 22px;
  line-height: 28px;
  font-weight: 500;
  color: $main-text-color;

  @media (min-width: 768px) {
    order: 0;
  }

  &--tel {
    letter-spacing: 1.1px;
  }
}
