.section-2 {
  position: relative;

  .wrapper {
    position: relative;
    z-index: 1;

    @media (min-width: 768px) {
      padding: 65px 80px 60px 80px;
    }

    @media (min-width: 1900px) {
      padding: 65px 306px 60px 354px;
    }
  }
}

.switcher {
  margin-bottom: 45px;

  .nav-tabs {
    position: relative;
    height: 48px;
    border: none;

    @media (min-width: 576px) {
      max-width: 600px;
      margin: 0 auto;
    }

    @media (min-width: 768px) {
      max-width: 667px;
    }

    @media (min-width: 1200px) {
      max-width: 780px;
    }
  }

  .nav-tabs .nav-item {
    position: absolute;
    height: 48px;

    &:first-child {
      width: 52%;
      top: 0;
      left: 0;
    }

    &:last-child {
      width: 52%;
      top: 0;
      right: 0;
    }
  }

  .nav-link {
    padding: 8px 10px 10px 10px;
    text-align: center;
    font-weight: 500;
    font-size: 23px;
    letter-spacing: 0.532764px;
    color: rgba(0, 0, 0, 0.25);
    border-radius: 50px;
    background-color: $secondary-button;
    border: 1px solid #dfdfdf;
    box-shadow: 0 5px 7px -4px rgba(0, 0, 0, 0.2);
  }

  .nav-tabs .nav-item.show .nav-link,
  .nav-tabs .nav-link.active {
    background-color: $button-color;
    color: #fff;
    z-index: 2;
    position: relative;
    border-color: $button-color;
  }
}

.tab-content {
  @media (min-width: 768px) {
    max-width: 640px;
    margin: 0 auto;
  }

  @media (min-width: 992px) {
    max-width: 780px;
  }
}


.balance-summary__row {
  padding: 25px 25px 25px 25px;
  background-color: #fafafa;
  border: 1px solid #979797;
  border-radius: 10px;

  @media (min-width: 567px) {
    padding: 19px 34px 15px 34px;
  }
}

.balance-summary__balance-info {
  @media (min-width: 576px) {
    padding-left: 30px;
  }

  @media (min-width: 768px) {
    padding-left: 88px;
  }
}

.balance-summary__text,
.balance-summary__balance {
  font-size: 22px;
  font-weight: 500;
  margin-bottom: 28px;
  letter-spacing: 0.532764px;
  text-align: center;

  @media (min-width: 576px) {
    text-align: left;
  }
}

.balance-summary__text {
  margin-bottom: 15px;

  @media (min-width: 576px) {
    margin-bottom: 28px;
  }
}

.balance-summary__balance {
  display: flex;
  flex-direction: column;
  justify-content: center;

  @media (min-width: 576px) {
    display: block;
  }
}

.balance-summary__balance-value {
  margin-left: 10px;

  @media (min-width: 576px) {
    margin-left: 0;
  }
}

.balance-summary__value {
  font-size: 31px;
  font-weight: 500;
  text-align: center;

  @media (min-width: 576px) {
    text-align: left;
  }
}

.transaction-information {
  background-color: $button-color;
  color: #fff;
  padding: 25px 25px 25px 25px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  margin-bottom: 56px;

  @media (min-width: 576px) {
    padding: 50px 34px 30px 34px;
  }
}

.transaction-information-lock__col {
  margin-bottom: 30px;

  @media (min-width: 576px) {
    margin-bottom: 0;
  }
}

.transaction-information-lock {
  max-width: 214px;
  margin: 0 auto;

  @media (min-width: 576px) {
    margin: 0;
  }
}

.transaction-information-earn {
  @media (min-width: 576px) {
    padding-left: 30px;
  }

  @media (min-width: 768px) {
    padding-left: 86px;
  }
}

.transaction-information__text {
  font-size: 22.54px;
  letter-spacing: 0.532764px;
  margin-bottom: 26px;
  text-align: center;

  @media (min-width: 576px) {
    text-align: left;
  }
}

.transaction-information__calendar {
  text-align: center;
  font-size: 31px;
  line-height: 31px;
  letter-spacing: 0.741236px;
  font-weight: 500;
  margin-bottom: 40px;
  display: flex;
  justify-content: center;
}

.transaction-information__separator {
  display: inline-block;
  padding: 0 12px;
  font-size: 0;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    top: -1px;
    left: calc(50% - 2px);
    width: 4px;
    height: 36px;
    background-color: #fff;
  }
}

.transactionion-information__range-values {
  display: flex;
  justify-content: space-between;
}

.transactionion-information__min-value,
.transactionion-information__max-value {
  font-size: 14px;
  letter-spacing: 0.324291px;
}

.transaction-information__currency {
  margin-bottom: 29px;
  display: flex;
  justify-content: center;

  @media (min-width: 576px) {
    display: block;
  }
}

.transaction-information__currency-value {
  font-size: 33px;
  font-weight: 600;
}

.transaction-information__currency-info {
  width: 188px;
  display: flex;
  justify-content: space-between;

  @media (min-width: 576px) {
    width: 160px;
  }

  @media (min-width: 992px) {
    width: 170px;
  }

  @media (min-width: 1900px) {
    width: 188px;
  }
}

.transaction-information__currency-icon {
  display: flex;

  .currency-icon {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-right: 10px;
  }

  .currency-name {
    font-size: 24.5px;
    letter-spacing: 0.579091px;
    font-weight: 500;
  }
}

.transaction-information__percentage-information {
  display: flex;
  justify-content: center;
  margin-bottom: 0;

  @media (min-width: 576px) {
    display: block;
  }
}

.rangeslider {
  background: rgba(0,0,0,0.3);
}

.rangeslider__fill {
  background: #fff;
  box-shadow: 0 0 0 0;
  -webkit-box-shadow: 0 0 0 0;
}

.rangeslider--horizontal {
  height: 8px;
}

.rangeslider--horizontal .rangeslider__handle {
  top: -4px;
}

.rangeslider__handle {
  width: 16px;
  height: 16px;
  border-color: #fff;
  background-image: none;
  box-shadow: 0 0 0 0;
  -webkit-box-shadow: 0 0 0 0;
}

.rangeslider__handle:after {
  content: none;
}

.make-button {
  display: flex;
  justify-content: center;
}

.custom-select-container {
  width: 100%;
  margin-bottom: 20px;

  @media (min-width: 768px) {
    margin-bottom: 25px;
  }

  @media (min-width: 1200px) {
    margin-bottom: 0;
  }
}

.balance-summary__dropdown {
  display: flex;
  justify-content: center;

  @media (min-width: 576px) {
    display: block;
  }
}

.select2-container {
  width: 150px !important;
  outline: none;
  margin: 0 auto;

  @media (min-width: 1200px) {
    width: 140px !important;
  }

  .selection:focus {
    outline: none;
  }
}

.select2-container--default
.select2-selection--single {
  width: 100%;
  height: 42px;
  border-radius: 20px;
  background-color: #F0ECEC;
  border-color: #cfcfcf;

  &:focus {
    border: 1px solid #666666;
    outline: none;
  }
}

.select2-container--default
.select2-selection--single:focus {
  border-color: #cfcfcf;
}

.select2-container--open
.select2-dropdown--below {
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  border-color: #cfcfcf;
}

.select2-container--open
.select2-dropdown--above {
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.select2-container--default 
.select2-results__option--highlighted[aria-selected] {
  background-color: #F0ECEC;
}

.select2-dropdown--above {
  .select2-results {
    .select2-results__option:first-child {
      border-top-left-radius: 20px;
      border-top-right-radius: 20px;

      &.select2-results__option--highlighted {
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;
      }
    }
  }
}

.select2-dropdown {
  border: 1px solid #666666;
}

.select2-dropdown--below {
  .select2-results {
    .select2-results__option:last-child {
      border-bottom-left-radius: 20px;
      border-bottom-right-radius: 20px;

      &.select2-results__option--highlighted {
        border-bottom-left-radius: 20px;
        border-bottom-right-radius: 20px;
      }
    }
  }
}

.select2-container--default
.select2-results>.select2-results__options {
  max-height: 400px;
}

.select2-container
.select2-selection--single
.select2-selection__rendered {
  height: 42px;
  padding: 6px 15px 8px 15px;

  span {
    font-size: 24.5px;
    color: #666666;
    font-weight: 500;
    display: flex;

    img {
      margin-right: 8px;
    }
  }
}

li.select2-results__option {
  span {
    font-size: 24.5px;
    color: #666666;
    font-weight: 500;
    display: flex;

    img {
      margin-right: 10px;
      margin-right: 10px;
    }
  }
}

.select2-container--default
.select2-selection--single
.select2-selection__arrow {
  height: 42px;
  font-size: 15px;
  line-height: 20px;
}

.select2-container--default.select2-container--open.select2-container--below
.select2-selection--single,
.select2-container--default.select2-container--open.select2-container--below
.select2-selection--multiple {
  border-bottom: none;
}

.select2-search--dropdown {
  display: none;
  padding: 4px;
}

.select2-results__option {
  min-height: 42px;
  padding: 6px 24px;
  background-color: #F0ECEC;
}

.select2-container--default
.select2-results__option[aria-selected=true] {
  background-color: #d6d6d6;

  span {
    color: #666666;
  }
}

.select2-container--default
.select2-results__option--highlighted[aria-selected] {
  background-color: $button-color;

  span {
    color: #fff;
  }
}

.select2-container--default
.select2-selection--single
.select2-selection__arrow {
  width: 40px;
}

.select2-container--default
.select2-selection--single
.select2-selection__arrow b {
  border: 2px solid #666666;
  border-top: none;
  border-left: none;
  height: 12px;
  left: calc(50% - 6px);
  top: calc(50% - 8px);
  width: 12px;
  transform: rotate(45deg);
  transform-origin: center;
  transition: all 0.3s ease;
}

.select2-container--default.select2-container--open
.select2-selection--single
.select2-selection__arrow b {
  border: 2px solid #666666;
  border-top: none;
  border-left: none;
  top: calc(50% - 4px);
  transform: rotate(-135deg);
  transition: all 0.3s ease;
}
